<template>
  <div class="powerSet">
    <div class="powerSet-box">
      <p class="power-intr">审核权限变更</p>
      <a href="javascript:;" class="btn-removePower"
         :class="{blue: batchSelectNum > 0,gray:batchSelectNum === 0}"
         @click="change('oneCancle')"
      >取消权限</a>
      <a href="javascript:;" class="btn-resetPower"
         :class="{blue: batchSelectNum > 0,gray:batchSelectNum === 0}"
         @click="change('oneResume')"
      >恢复权限</a>
      <a href="javascript:;" class="btn-allRemove"
         @click="change('allCancle')"
      >全部取消权限</a>
      <a href="javascript:;" class="btn-allReset"
         @click="change('allResume')"
      >全部恢复权限</a>
      <a href="javascript:;" class="btn-return" @click="$emit('changeFormComponent','index-details-form')" >返回</a>
    </div>
  </div>
</template>

<script>
    export default {
      name: "IndexDetailsPowerSet",
      props: ["batchSelectNum"],
      methods:{
        change(name){
          if(name.indexOf("all") > -1 || this.batchSelectNum > 0){
            this.$emit('powerSet',name);
            this.$store.commit('changeDialog','deleteAuthority');
          }
        }
      }
    }
</script>

<style scoped>

</style>



