<template>
  <th>{{chName}}
    <span class="down"
          @click="sortData(orderBy)"
          :class="{on: active === 'down' && orderBy === judge_resume_params.order_by}"
          v-if="keyName !== 'user_id'"
    ></span>
    <span class="up"
          :class="{on: active === 'up' &&  orderBy === judge_resume_params.order_by}"
          @click="sortReverseData(orderBy)"
          v-if="keyName !== 'user_id'"
    ></span>
    <!--暂时不支持审判者帐号排序。-->
  </th>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
      name: "SortTableBtn",
      props: ["chName","keyName","orderBy"],
      data(){
        return{
          active: 'down',
        }
      },
      computed: {
        ...mapState(["judge_resume","judge_resume_params"]),
      },
      methods:{
        isRepeatClick(orderBy,is_descending){
          return orderBy === this.judge_resume_params.order_by && is_descending === this.judge_resume_params.is_descending
        },
        sortData(orderBy){
          if(this.isRepeatClick(orderBy,true)) {
            this.orderInit(orderBy);
            return;
          }
          this.active = 'down';
          this.$store.commit("judge_resume_params",{
            order_by: orderBy,
            is_descending: true,
          });
          //this.$store.commit("changeJudgeResume",this.judge_resume.sort(this.compareObj(key)));;//根据对象的b属性的大小排序
        },
        sortReverseData(orderBy){
          if(this.isRepeatClick(orderBy,false)) {
            this.orderInit(orderBy);
            return;
          }
          this.active = 'up';
          this.$store.commit("judge_resume_params",{
            order_by: orderBy,
            is_descending: false,
          });
          //this.$store.commit("changeJudgeResume",this.judge_resume.sort(this.compareReverseObj(key)));;//根据对象的b属性的大小排序
        },
        orderInit(){
          this.active = 'down';
          this.$store.commit("judge_resume_params",{
            order_by: 1,//1:审核单数默认按审核单数来查
            is_descending: true,
          });
        }
      }
        /*compareObj(propertyName){
          return function (obj1,obj2) {
            if(obj1[propertyName] > obj2[propertyName]){
              return 1;
            }else if(obj1[propertyName] < obj2[propertyName]){
              return -1;
            }else{
              return 0;
            }
          }
        },
        compareReverseObj(propertyName){
          return function (obj1,obj2) {
            if(obj1[propertyName] > obj2[propertyName]){
              return -1;
            }else if(obj1[propertyName] < obj2[propertyName]){
              return 1;
            }else{
              return 0;
            }
          }
        }*/

    }
</script>

<style scoped>

</style>



