<template>
  <div class="powerTable-box">
    <table cellspacing="1" class="powerTable table">
      <thead>
      <tr>
        <th v-if="formComponent === 'index-details-power-set'"
            rowspan="2"
        >已选择
          <span id="chooseNum">{{deleteAuthorityId.length}}</span>人
        </th>
        <sort-table-btn rowspan="2"
                        class="btn-rank"
                        v-for="(sortBtn,index) in sortBtnData2"
                        :ch-name="sortBtn.ch"
                        :key-name="sortBtn.key"
                        :order-by="sortBtn.order_by"
                        :key="index"
                        :class="sortBtn.className"
        ></sort-table-btn>
        <!--<th class="btn-rank" rowspan="2">审判者账号
          <span class="down on"></span><span class="up"></span>
        </th>
        <th class="btn-rank" >审判总单数
          <span class="down on"></span><span class="up"></span>
        </th>-->
        <th rowspan="2">无违规判定单数</th>
        <th rowspan="2">有违规判定单数</th>
        <th colspan="2" style="width: 223px;">审判结果准确率</th>
        <th colspan="2" style="width: 223px;">每单审核平均用时</th>
        <th rowspan="2">审核权限变更</th>
      </tr>
      <tr>
        <sort-table-btn v-for="(sortBtn,index) in sortBtnData"
                        class="btn-rank"
                        :ch-name="sortBtn.ch"
                        :key-name="sortBtn.key"
                        :order-by="sortBtn.order_by"
                        :key="index"
                        :class="sortBtn.className"
        ></sort-table-btn>
        <!--<th >近一月
          <span class="down on" @click="sortData('month_accuracy')"></span>
          <span class="up" @click="sortReverseData('month_accuracy')"></span>
        </th>
        <th class="minWidth btn-rank">近一周
          <span class="down on"></span>
          <span class="up"></span>
        </th>
        <th class="minWidth btn-rank">近一月
          <span class="down on"></span>
          <span class="up"></span>
        </th>-->
      </tr>
      </thead>
      <tbody>
        <!--<tr>
          <td>46023354</td><td>3000</td><td>2000</td><td>1000</td><td>85%</td><td>83%</td><td>5 min</td><td>4.5 min</td><td><a href="javascript:;" class="btn-remove">取消权限</a></td>
        </tr>.slice(judge_resume_start,judge_resume_end)-->
        <tr v-for="(resume,index) in judge_resume"
            :key="index">
          <td v-if="formComponent === 'index-details-power-set'">
            <a href="javascript:;"
               class="check spr"
               @click="batchSelect(resume.user_id)"
               :class="{choosed: deleteAuthorityId.includes(resume.user_id)}"
            ></a>
          </td>
          <td @click="queryAccountsDetails(resume.user_id,
                                           resume.month_accuracy,
                                           resume.week_accuracy,
                                           resume.level)
          ">{{resume.user_id}}</td>
          <td>{{resume.total_case_num}}</td>
          <td>{{resume.innocent_case_num}}</td>
          <td>{{resume.guilty_num}}</td>
          <td>{{resume.week_accuracy}}</td>
          <td>{{resume.month_accuracy}}</td>
          <td>{{resume.week_judge_period}} min</td>
          <td>{{resume.month_judge_period}} min</td>
          <td>
            <a href="javascript:;" class="btn-remove"
               v-if="resume.level === -1"
               @click="popShowDeleteAuthority(resume.user_id,$op_type.resume)">恢复权限</a>
            <a href="javascript:;" class="btn-remove"
               v-else
               @click="popShowDeleteAuthority(resume.user_id,$op_type.delete)">取消权限</a>

          </td>
        </tr>
      </tbody>
    </table>
    <my-dialog @deleteAuthority="deleteAuthority"></my-dialog>
  </div>
</template>

<script>
    import modify_user_auth from "../mixins/modify_user_auth"
    import {mapState} from 'vuex'
    import SortTableBtn from "./SortTableBtn";
    import MyDialog from "./MyDialog";
    import {setStore,getStore} from "../assets/js/storage"
    export default {
      name: "IndexDetailsTable",
      components: {MyDialog, SortTableBtn},
      mixins: [modify_user_auth],
      props: ["formComponent","powerSetEvent"],
      data(){
        return{
          op_type: {
            delete: 2,//2：取消(可恢复）
            //3：彻底删除
            add: 1,   //1：授权
            resume: 4,//4：恢复
          },
          default_dst_level: 1,
          deleteAuthorityId: [],
          sortBtnData:[
            {ch:'近一周',key:'week_accuracy',className: 'minWidth',order_by: 2},
            {ch:'近一月',key:'month_accuracy',className: 'minWidth',order_by: 3},
            {ch:'近一周',key:'week_judge_period',className: 'minWidth',order_by: 4},
            {ch:'近一月',key:'month_judge_period',className: 'minWidth',order_by: 5}
            ],
          sortBtnData2:[
            {ch:'审判者账号',key:'user_id',order_by: 0},
            {ch:'审判总单数',key:'total_case_num',order_by: 1},
          ],
          pageData: {
            limit: 10,//本次要查询的个数
          },
        }
      },
      computed: {
        ...mapState(["resumePageConfig","judge_resume"]),
        allDeleteAuthorityId(){
          let arr = [];
          this.judge_resume.forEach( (item)=>{
            arr.push(item.user_id);
          });
          return arr;
        },
      },
      watch:{
        formComponent(newVal){
          if(newVal === "index-details-power-set"){
            this.deleteAuthorityId = [];//重置选择的id数组
          }
        }
      },
      methods:{
        queryAccountsDetails(user_id,month_accuracy,week_accuracy,level){
          this.$router.push({ path: 'accountsDetails', query: { userId: user_id }});
          setStore('week_accuracy',week_accuracy);
          setStore('month_accuracy',month_accuracy);
          setStore('user_id',user_id);
          if(level === -1){
            setStore('op_type',this.$op_type.resume);
            this.$store.commit('indexSelectedOpType', this.$op_type.resume);
          }else{
            setStore('op_type',this.$op_type.delete);
            this.$store.commit('indexSelectedOpType', this.$op_type.delete);
          }
          //this.$store.commit("changeAccountsParams",accountsParams)
        },
        deleteAuthority(data){
          switch(this.powerSetEvent)
          {
            case 'oneCancle':
              this.modify_user_auth(this.$op_type.delete,this.deleteAuthorityId,data,this.default_dst_level);
              break;
            case 'oneResume':
              this.modify_user_auth(this.$op_type.resume,this.deleteAuthorityId,data,this.default_dst_level);
              break;
            case 'allCancle':
              this.modify_user_auth(this.$op_type.delete,this.allDeleteAuthorityId,data,this.default_dst_level);
              break;
            case 'allResume':
              this.modify_user_auth(this.$op_type.resume,this.allDeleteAuthorityId,data,this.default_dst_level);
              break;
            default:
              let op_type = getStore('op_type');
              this.modify_user_auth(op_type,this.deleteAuthorityId,data,this.default_dst_level);
              break;
          };
        },
        popShowDeleteAuthority(user_id,op_type){
          this.deleteAuthorityId = [];
          this.deleteAuthorityId.push(user_id);
          this.$store.commit('changeDialog','deleteAuthority');
          setStore('op_type',op_type);
        },
        batchSelect(user_id){
          if(this.deleteAuthorityId.includes(user_id)){
            this.deleteAuthorityId.splice(this.deleteAuthorityId.indexOf(user_id), 1);
          }else{
            this.deleteAuthorityId.push(user_id);
          }
          this.$emit('batchSelectNum',this.deleteAuthorityId.length)
        },
      }
    }
</script>

<style scoped>

</style>



