<template>
  <div>
    <my-header></my-header>
    <!--顶部白底部分 start-->
    <index-details-form v-show="formComponent === 'index-details-form'"
                        @changeFormComponent="changeFormComponent"
    ></index-details-form>
    <index-details-power-set v-show="formComponent === 'index-details-power-set'"
                             @changeFormComponent="changeFormComponent"
                             @powerSet="powerSet"
                             :batch-select-num="batchSelectNum"
    ></index-details-power-set>
    <!--顶部白底部分 end-->
    <index-details-table :form-component="formComponent"
                         @batchSelectNum="batchSelect"
                         :power-set-event="powerSetEvent"
    ></index-details-table>
    <my-paginator :page-config="resumePageConfig" :page-name="pageName"></my-paginator>
  </div>
</template>

<script>
    import IndexDetailsForm from "../components/IndexDetailsForm";
    import IndexDetailsTable from "../components/IndexDetailsTable";
    import MyPaginator from "../components/MyPaginator";
    import {mapState} from "vuex";
    import IndexDetailsPowerSet from "../components/IndexDetailsPowerSet";
    import MyDialog from "../components/MyDialog";
    import MyHeader from "../components/MyHeader";
    export default {
      name: "AllDataDetails",
      components: {MyHeader, MyDialog, IndexDetailsPowerSet, MyPaginator, IndexDetailsTable, IndexDetailsForm},
      data(){
        return{
          formComponent: 'index-details-form',
          pageName: 'resume',
          batchSelectNum: 0,
          powerSetEvent: "",//权限设置的事件
        }
      },
      computed:{
        ...mapState(["resumePageConfig","dialogName"]),
      },
      methods:{
        changeFormComponent(data){
          this.formComponent = data;
        },
        batchSelect(data){
          this.batchSelectNum = data;
        },
        powerSet(data){
          this.powerSetEvent = data;
        }
      }
    }
</script>

<style scoped>

</style>



