<template>
  <div class="auditFiltrate">
    <div class="auditFiltrate-box">
      <div class="infor-box">
        <p class="start-time">开始时间</p>
        <p class="end-time">结束时间</p>
        <p class="account">账号</p>
      </div>
      <div class="input-box">
        <DatePicker class="date-start"
                    type="date"
                    :placeholder="initStartTime"
                    v-model="formData.start_time"></DatePicker>
        <DatePicker class="date-end"
                    type="date"
                    :placeholder="initEndTime"
                    v-model="formData.end_time"></DatePicker>
        <input class="account-info" type="text" v-model="formData.query_user">
      </div>
      <div class="infor-box">
        <p class="start-time">审核结果筛选： 审核总单数</p>
        <p class="end-time">审核准确率</p>
      </div>
      <div class="input-box">
        <input class="date-start"
               v-model="formData.case_num"
               type="number"
               onkeyup="this.value=this.value.replace(/[^\d]/g,'')" onafterpaste="this.value=this.value.replace(/[^\d]/g,'') "
        >
        <input class="date-end"
               v-model="formData.accuracy"
               type="number"
               onkeyup="this.value=this.value.replace(/[^\d]/g,'')" onafterpaste="this.value=this.value.replace(/[^\d]/g,'') "
        > %
        <p class="tip">提示：输入单数、准确率后，查询会显示大于等于输入值的结果</p>
      </div>

      <div class="select">
        <a href="javascript:;" class="btn-select" @click="checkTime(judge_resume_params)">查询</a>
        <a href="javascript:;" class="btn-deal" @click="$emit('changeFormComponent','index-details-power-set');">批量处理</a>
      </div>
    </div>
  </div>
</template>

<script>
    import {query_user_judge_resume} from "../apis/getData";
    import {setStore} from "../assets/js/storage";
    import {mapState} from "vuex"
    export default {
      name: "IndexDetailsForm",
      data(){
        return{
          formData:{
            query_user: "",//审判人员id
            start_time: "",//开始时间
            end_time: "",//结束时间
            case_num: "",//审核总单数
            accuracy: "",//准确率条件
          },
          pageData: {
            offset: 0,//查询偏移量
            limit: 10,//本次要查询的个数
          },
        }
      },
      computed:mapState({
        resumeCurrent: state => state.resumePageConfig.current,
        judge_resume: state => state.judge_resume,
        judge_resume_params: state => state.judge_resume_params,
        isReloadData: state => state.isReloadData,
        initStartTime(){
          return `${this.$timeFormat(new Date().getTime()-1000*60*60*24).slice(0,10)} 00:00:00`;
        },
        initEndTime(){
          return `${this.$timeFormat(new Date().getTime()-1000*60*60*24).slice(0,10)} 23:59:59`;
        }
      }),
      watch:{
        resumeCurrent(newVal){
          this.pageData.offset = (newVal-1)*this.pageData.limit;
          this.checkTime(this.judge_resume_params);
        },
        judge_resume_params(newVal){
          if(this.resumeCurrent === 1){
            this.checkTime(newVal);
          }else{
            this.$store.commit("resumePageCurrent",1);//点击排序按钮时分页要回到第一页
          }
        },
        isReloadData(newVal){
          if(newVal){
            this.checkTime(this.judge_resume_params);
            this.$store.commit('isReloadData',false);
          }
        }
      },
      mounted(){
        this.checkTime(this.judge_resume_params);
      },
      methods:{
        checkTime(params){
          let start_time = this.formData.start_time===""?this.initStartTime:this.$timeFormat(this.formData.start_time);
          let end_time = this.formData.end_time===""?this.initEndTime:this.$timeFormat(this.formData.end_time);
          if(start_time > end_time){
            this.$onlyTxtPopShow(this.$errTxt.selectTimeErr);
            return;
          };
          let formatTime = {
            start: this.formData.start_time? this.$timeFormat(this.formData.start_time) : this.initStartTime,
            end : this.formData.end_time? this.$timeFormat(new Date(this.formData.end_time).getTime()+1000*60*60*24-1) : this.initEndTime
          };
          let data = {
            game_id: 2577,//游戏id, 取0表示全量游戏
            //user_id: 0,//用户id写0，实际从cookie中获取
            src_type: 0,
            query_user: this.formData.query_user,//审判人员id
            start_time: formatTime.start,//开始时间
            end_time: formatTime.end,//结束时间
            case_num: this.formData.case_num,//审核总单数
            accuracy: this.formData.accuracy,//准确率条件
            offset: this.pageData.offset,//查询偏移量
            limit: this.pageData.limit,//本次要查询的个数
            order_by: params.order_by,
            /*按特定值排序,可取值为：
            0:user_id
            1:审核单数
            2:近一周准确率
            3:近一个月准确率
            4:近一周审核时长
            5:近一个月审核时长*/
            is_descending: params.is_descending ? 1 : 0,//是否降序，取1降序，取0升序，默认给1吧
          };
          query_user_judge_resume(data).then(response => {
            if (response.ret === 0) {
              this.$store.commit("changeJudgeResume",response.data.resume);
              this.$store.commit("resumePageTotal",response.data.total);
              if(response.data.length === 0 || response.data.total === 0){
                this.$onlyTxtPopShow(this.$errTxt.noData);
              }
              setStore('start_time',formatTime.start);
              setStore('end_time',formatTime.end);
            }else{
              this.formReset();
              if (response.ret === this.$code.LOGINTIMEOUT) {
                this.$loginOut();
              }else if (response.ret === this.$code.TOOLONGTIME.num) {
                this.$onlyTxtPopShow(this.$code.TOOLONGTIME.txt);
              }else if (response.ret === this.$code.NOAUTHORITY.num) {//和修改权限的接口返回码一致，提示文案不一致
                this.$onlyTxtPopShow(this.$errTxt.noUser);
              } else{
                this.$onlyTxtPopShow(this.$errTxt.getDataErr);
              };
            }
          });
        },
        formReset(){
          this.formData = {
            query_user: "",//审判人员id
              start_time: "",//开始时间
              end_time: "",//结束时间
              case_num: "",//审核总单数
              accuracy: "",//准确率条件
          };//重置表单
        },
      }
    }
</script>

<style scoped>

</style>



